.container {
  display: flex;
  background-color: white;
  color: black;
  flex-direction: column;
  padding: 15px;
  position: sticky;
  z-index: 1000;
  width:340px;
  border-radius:5px;
  align-content: center;
}

.titleContainer{
  display: flex;
  align-items: center;
  gap: 5px;
  width: 100%;
  margin-bottom: 20px;
  flex-direction: column;
}

.Title {
  color: #3c009d;
  font-size: 28px;
  font-weight: 700;
}

.underline{
  background: #3c009d;
  width: 41px;
  height: 5px;
  border-radius: 1px;
  margin-top: 8px;
}

.form {
  display: flex;
  background-color: floralwhite;
  flex-direction: column;
  align-items: center;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  padding: 20px 5px;
}

/* Label.module.css */
.formLabel {
  color: var(--black-color);
  box-sizing: border-box;
}

/* Input.module.css */
.Input {
  width: 280px;
  margin-bottom: 20px;
  background: white;
  border: var(--none-border);
  outline: none;
  padding: 10px;
  font-size: var(--s-font-size);
  color: var(--black-color);
  text-shadow: var(--text-shadow);
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  box-shadow: var(--text-shadow);
  transition: border 0.3s ease-in-out;
}

.Input:focus,
.Input:hover {
  border: 1px solid #966cd1;
}

/* Button.module.css */
.btnSubmit {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: bold;
  gap: 10px;
  color: white;
  padding: 15px 15px;
  background: #9758ef;
  border: none;
  border-radius: 30px;
  width: 90%;
  margin-top: 10px;
  font-size: 18px;
}

.btnSubmit:focus,
.btnSubmit:hover {
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.24);
  background-color: #5f20b6;
  color: white;
  border: 1px pink;
}

.formLabel {
  display: flex;
  margin-bottom: 3px;
}
